@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{box-sizing:border-box;font-size:12px}body{margin:0;background-color:#bfbdbb;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}@media screen and (min-width: 992px){*{font-size:16px}}@media screen and (min-width: 768px){*{font-size:14px}}@media screen and (min-width: 480px){*{font-size:13px}}

.App{width:98%;margin:1%;padding:1rem;border:2px solid #fff;background-color:#ebe9e8}

.header{margin:0;font-size:1.2rem;text-align:start;color:#2e2e2d;font-weight:500}

.top-databoard{margin:1.5rem 0}@media screen and (min-width: 481px){.top-databoard{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:1rem}}

.single-data{width:100%;text-align:start;margin:auto;padding:0;font-weight:400;font-size:1rem;border-bottom:2px solid #737272;border-right:2px solid #737272;background-color:#f2f2f2;margin-bottom:1.5rem}.single-data>div:not(:last-child){margin:0.5rem 0 0.5rem 0.5rem}.single-data__total{font-size:2rem}.single-data__stat{display:flex;direction:row;justify-content:space-between;align-items:center}.single-data__title{margin-left:0;text-transform:uppercase;font-size:1rem}.single-data__percentage{margin-right:1rem}.single-data__filter{display:grid;grid-template-columns:auto 35px;grid-template-rows:auto}.single-data__stat-interval{display:flex;align-items:center;background-color:#bfbdbb;padding:0.5rem}.single-data__options{text-transform:uppercase;background:transparent;border:none}

.user-icon{padding:0.5rem;width:35px;height:35px;background-color:#9fed39}

.more-icon{padding:0.5rem;width:35px;height:35px;background-color:#ed4940}

.stat-icon{padding:0.5rem;width:35px;height:35px;background-color:#ed9c40}

.database-icon{padding:0.5rem;width:35px;height:35px;background-color:#3fc4d9}

@media screen and (min-width: 768px){.main-section{display:grid;grid-template-columns:50% 50%;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;grid-template-areas:"production sales-revenue" "summary sales-revenue"}}

.header-with-icons{display:flex;direction:row;justify-content:space-between;align-items:center;margin-bottom:1rem}.header__title{margin:0;font-size:1.2rem;text-align:start;color:#2e2e2d;text-transform:uppercase;font-weight:500}.icons__refresh,.icons__unchecked,.icons__dropdown{fill:#737272;padding:2px;width:20px;height:20px}.icons__refresh,.icons__unchecked{margin-right:1rem}

.import-export-button{width:10rem;display:flex;flex-direction:row;justify-content:space-between;align-items:center;background-color:#bfbdbb;margin:0.5rem 0}.import-export-button__title{text-transform:uppercase;font-size:0.8rem;text-align:start;padding-left:0.3rem}

.import-icon,.export-icon{padding:0.5rem;width:40px;height:30px;background-color:#737272}

.import-export-section{display:inline-flex;flex-direction:row;align-items:center}.import-export-section>div{margin-right:1rem}

.month-year-selector{width:7rem}.date-picker{width:100%;height:30px;padding-left:0.2rem;border-radius:0;border-bottom:2px solid #737272;border-right:2px solid #737272;background-color:#f2f2f2}

.production{margin:1.5rem 0;padding:0.5rem;border-bottom:2px solid #737272;border-right:2px solid #737272;background-color:#f2f2f2}.production__image{width:98%;margin:auto}@media screen and (min-width: 768px){.production{grid-area:production}}@media screen and (min-width: 480px){.production__filter{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0 1rem}}

.summary{margin:1.5rem 0;padding:0.5rem;border-bottom:2px solid #737272;border-right:2px solid #737272;background-color:#f2f2f2}.summary__bar{display:block;height:0.7rem;background-color:#bfbdbb}.summary__bar:not(:last-child){margin-bottom:0.5rem}@media screen and (min-width: 768px){.summary{grid-area:summary}}

.sales-revenue-section{margin:1.5rem 0;padding:0.5rem;border-bottom:2px solid #737272;border-right:2px solid #737272;background-color:#f2f2f2}.sales-revenue-section__title{margin-left:1rem}.sales-revenue-section__country{width:100%;margin:1rem auto}@media screen and (min-width: 768px){.sales-revenue-section{grid-area:sales-revenue}}

.filter{display:flex;flex-direction:column;margin:1.5rem 0;padding:0.5rem}.filter>div{margin:0.5rem 0}.filter__interval{display:flex;align-items:center}@media screen and (min-width: 620px){.filter{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:0 1rem}}@media screen and (min-width: 768px){.filter{margin:0}}

